@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;min-height:100vh;overflow-x:hidden;box-sizing:border-box}body #root{min-height:100vh;display:grid;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}input{padding:0}p{margin:0}

.App{display:grid;min-height:100vh;width:100%;grid-template-areas:'header header header' 'content content content' ' . . . ' 'footer footer footer';grid-template-rows:auto auto minmax(0px, 1fr) auto}.Preloader{width:100px}.btn{cursor:pointer;border:1px solid grey;text-transform:uppercase;font-weight:500;letter-spacing:1px;border-radius:10px;color:black}.btn:hover{color:white;background-color:#818181}

@font-face{font-family:'Stint Ultra Condensed';src:url(/todo-list/static/media/StintUltraCondensed-Regular.d35634cf.eot);src:local("☺"),url(/todo-list/static/media/StintUltraCondensed-Regular.3da51987.woff) format("woff"),url(/todo-list/static/media/StintUltraCondensed-Regular.2ff4dc82.ttf) format("truetype"),url(/todo-list/static/media/StintUltraCondensed-Regular.7c890582.svg) format("svg");font-weight:normal;font-style:normal}.header{grid-area:header;display:grid;grid-template-columns:1fr 1fr 1fr;justify-items:center;-webkit-align-items:center;align-items:center;width:100vw;height:8vh;color:black;border-bottom:solid 1px #99999942;box-shadow:0 1px 1px #d6d6d6;background-color:#fff;top:0;left:0}.header__logo{grid-column:2;font-size:40px;font-family:'Stint Ultra Condensed', cursive}@media (max-width: 767px){.header__logo{font-size:30px}}.header__loginBlock{-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;grid-column:3}.header__loginBlock img{width:20px;margin:-5px 15px}.header__loginBlock .loginBlock__item{font-size:15px;text-transform:uppercase;color:black;text-decoration:none}@media (max-width: 767px){.header__loginBlock .loginBlock__item{display:none}}

.footer{grid-area:footer;display:grid;-webkit-align-items:center;align-items:center;height:6vh;width:100vw;color:black;text-align:center;border-top:solid 1px #99999942;box-shadow:0 -1px 1px #d6d6d6}

.login{display:grid}.login__logout-btn{padding:15px 30px}.form{display:grid;justify-self:center;-webkit-align-items:center;align-items:center;justify-items:center;grid-template-columns:30% 1fr;width:200px;grid-template-areas:"email email" "password password" "rememberme remembermeText" "error error" "login login"}.form input{margin:10px;padding:5px 10px}.form__input_email{grid-area:email}.form__input_password{grid-area:password}.form__checkbox_rememberme{grid-area:rememberme}.form__checkbox_rememberme input{cursor:pointer;height:15px;width:15px}.form__input_text{grid-area:remembermeText;-webkit-user-select:none;-ms-user-select:none;user-select:none;padding:0 0 3px 0;cursor:pointer}.form__btnlogin{grid-area:login;width:150px;font-weight:700;padding:15px 0;margin:20px 0 15px}.error{display:grid;grid-area:error;grid-template-areas:"captcha" "message"}.error__message{text-align:center;grid-area:message;outline:solid 1px red;color:red;padding:5px 10px;margin:15px 0}.error__captcha{margin:10px 0 0 0;display:grid;grid-area:captcha;grid-template-areas:"img" "input"}.error__captcha_img{grid-area:img;justify-self:center;display:block;width:150px}.error__captcha_input{grid-area:input}

.formControl.error{outline:solid 1px red}.formControl.error span{color:red}.formSummatyError{text-align:center;outline:solid 1px red;color:red;display:grid}.formSummatyError div{-webkit-align-self:center;align-self:center;padding:15px}

.modal{height:100vh;width:100vw;background-color:rgba(75,75,75,0.5);position:fixed;top:0;left:0;display:grid;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center;transition:all ease 0.3s;cursor:pointer;pointer-events:none;opacity:0;z-index:10}.modal.active{z-index:10;transition:all ease 0.5s;pointer-events:all;opacity:1}.modal__content{cursor:default;background-color:#fff;justify-items:center;border-radius:15px;width:50vw;padding:30px;position:relative;-webkit-transform:scale(0.5);transform:scale(0.5);transition:all 0.5s;display:grid}.modal__content.active{-webkit-transform:scale(1);transform:scale(1)}.modal_title{text-align:center;font-size:18px}.modal_input{width:150px;padding:5px 10px;margin:15px 0}.modal_btn{margin:15px 0 10px;line-height:30px;width:100px}.modal_exit{position:absolute;top:2vh;right:2vw;cursor:pointer}.modal_exit img{height:50px;width:50px}

.lds-ripple{display:inline-block;position:relative;width:80px;height:80px}.lds-ripple div{position:absolute;border:4px solid #000;opacity:1;border-radius:50%;-webkit-animation:lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite}.lds-ripple div:nth-child(2){-webkit-animation-delay:-0.5s;animation-delay:-0.5s}@-webkit-keyframes lds-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}@keyframes lds-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}

.main{grid-area:content;display:grid;grid-gap:60px;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center;padding:50px 5%;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr))}@media (max-width: 767px){.main{padding:5% 5%}}@media (max-width: 767px){.main{padding:0}}.main .addList{display:grid;grid-column:1 / -1}.main .addList a{cursor:pointer;text-align:center;justify-self:center}.main .addList a img{width:40px}.preloader-wrapper{grid-row:2 / span 2;grid-column:1 / span 3;justify-self:center;align-self:center}.testApp{grid-area:2 / 1 / span 2 / span 3;justify-self:center;text-align:center;align-self:center}

.delwar{display:grid;grid-template-areas:'title title' 'text text' 'yes no'}.delwar__title{grid-area:title}.delwar__text{margin-top:20px;grid-area:text}.delwar__btn{justify-self:center;width:50%;line-height:40px;margin:40px 0 0 0;font-weight:400;font-family:'Roboto Condensed', sans-serif;font-size:16px}.delwar .btn-yes{grid-area:yes;border:red 3px solid}.delwar .btn-yes:hover{box-shadow:0px 0px 10px 7px #ff000045;background-color:red}.delwar .btn-no{grid-area:no}.delwar .btn-no:hover{border:green 1px solid;box-shadow:0px 0px 7px 3px green;background-color:green}

.list{display:grid;grid-template-rows:50px 1fr;position:relative;align-self:start;justify-items:center;z-index:2;background-color:#e0e0e0}.list:hover .add-task{-webkit-transform:translateY(100%);transform:translateY(100%);opacity:1}.list__title{overflow:hidden;position:relative;display:grid;justify-items:start;-webkit-align-items:center;align-items:center;outline:2px solid #80808070;width:100%;height:50px}.list__title:hover .help-buttons{-webkit-transform:translateY(0);transform:translateY(0)}.list__title_input{padding:0 0 0 15px;font-size:20px;display:inline-block}.list__title_span{font-size:20px;padding:0 15px;-webkit-justify-content:start;justify-content:start;-webkit-align-content:center;align-content:center}.list__tasks{width:100%}.list .input{outline:none;border:none;box-sizing:border-box;width:100%;height:100%;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif}.help-buttons{-webkit-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden;-webkit-transform:translateY(100%);transform:translateY(100%);transition:all ease-in-out 0.35s;position:absolute;top:0;right:0;width:100%;height:100%}.help-buttons__item{cursor:pointer}.help-buttons__item.info img{width:30px;position:absolute;top:20%;right:15%}.help-buttons__item.delete img{top:30%;right:4%;position:absolute;width:20px}.add-task{display:grid;justify-items:center;position:absolute;opacity:0;overflow:hidden;z-index:1;bottom:0;width:100%;transition:all ease-in-out 0.35s 0.1s;-webkit-transform:translateY(50%);transform:translateY(50%);padding:10px 0}.add-task img{cursor:pointer;width:25px}

.task{width:100%;display:grid;grid-template-columns:minmax(40px, 50px) 1fr;grid-template-rows:minmax(42px, auto);justify-items:center;-webkit-align-items:center;align-items:center;position:relative;overflow:hidden;border-bottom:1px #80808070 solid}.task:hover .help-buttons{-webkit-transform:translateY(0);transform:translateY(0)}.task__check-block{-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;display:grid;-webkit-align-items:center;align-items:center;height:100%}.task__check-block img{width:25px}.task__check-block_loading{cursor:default}.task__text{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:inherit;height:100%;justify-self:start;display:grid;-webkit-align-items:center;align-items:center;justify-items:start}.task__text .help-buttons{width:40px}.task__text .help-buttons__item.delete img{left:0}.task__text_input{z-index:-1;resize:none;height:100%;box-sizing:content-box;font-size:17px;padding:0 15px}.task__text_input:focus+.help-buttons{-webkit-transform:translateY(100%);transform:translateY(100%)}.task__text_span{font-size:17px;padding:0 15px}.task__text_span.done{text-decoration:line-through}

.add-list{display:grid;justify-items:center}.add-list_title{text-align:center;font-size:18px}.add-list_input{width:150px;padding:5px 10px;margin:15px 0}.add-list_btn{margin:15px 0 10px;line-height:30px;width:100px}

